@mixin boxSizingComponent {

    *, *:before, *:after {
        @extend .#{$defaultBoxSizingProperty}; }


    @each $type in $boxSizingTypes {
        .#{$type} {
            box-sizing: $type;

            &:before, &:after {
                @extend .#{$type}; } } } }
