@mixin horizontalAlignmentComponent {

    .#{$breakpointPrefix}x-centered, .#{$breakpointPrefix}horizontally-centered {
        @if $isFirstBreakpointPrefix {
            @extend .x-centered; }

        @else {
            margin: 0 auto; } } }
