$color-1: #FFFFFF;
$color-2: #f7f7f7;
$color-3: #0f0909;
$color-4: #ef0f00;
$color-5: #d10000;
$color-6: #ff9400;
$color-7: #eb8000;
$color-8: #00a80b;
$color-9: #009400;
$color-10: #dddddd;

//
//- components/grid-system
//- s - phone, m - tablet, l - desktop, xl - > desktop
//- $breakpointPrefix and $isFirstBreakpointPrefix are global variables that are changed during
//  compilation in the grid.sass and in the components/grid-system.sass; they are changed
//  in the initPrefix mixin; they are used in components and in the mixins
$breakpoints: s 0, m 641, l 1025, xl 1441;
$firstBreakpointPrefix: nth(nth($breakpoints, 1), 1);
$breakpointPrefix: null;
$noneBreakpointPrefix: true;
$isFirstBreakpointPrefix: false;


// components/max-width
$defaultMaxWidthPrefix: max-width;
$widthMax: 100;
$widthMin: 5;
$widthIncreasedBy: 5;


// components/margin, components/padding
$frameMaxSize: 50;
$frameSizeIncreasedBy: 5;
$frameSides: left right top bottom;


// components/html-body
$defaultFontSize: 16px;
$defaultLineHeight: 1.5;


// components/display-types, components/grid-system
$displayTypes: inline inline-block block flex none;
$positions: static relative absolute fixed;


// components/order, components/grid-system
$orderMax: 6;
$orderMin: 1;
$orderIncreasedBy: 1;


// components/columns, components/grid-system
$columnsMax: $orderMax;
$columnsMin: $orderMin;
$columnsIncreasedBy: $orderIncreasedBy;
$columnsRuleTypes: solid dashed dotted;


// components/cursor
$cursorTypes: auto default pointer wait help;


// components/list
$listStyleTypes: disc square decimal;
$listPositions: inside outside;


// components/float-clear
$floatTypes: left right none;
$clearTypes: left right both none;


// components/resize
$resizableLabels: x y both;
$resizableValues: horizontal vertical both;


// components/vertical-align
$valignTypes: baseline top middle bottom super sub;


// components/wrap
$wrapTypes: nowrap wrap wrap-reverse;


// components/box-sizing
$boxSizingTypes: border-box content-box padding-box;
$defaultBoxSizingProperty: nth($boxSizingTypes, 1);
