@mixin notesComponent {

	.note {
		@extend .display-block;
		@extend .rounded-2;
		margin-top: 16px;
		margin-bottom: 16px;
		border: 1px solid $color-10;
		border-left: 5px solid;
		padding: 16px;

		> p {
			&:first-of-type {
				margin-top: 0; }

			&:last-of-type {
				margin-bottom: 0; } }

		&-title, h2, h3, h4, h5, h6 {
			margin: 0 0 5px; }

		&-green {
			@extend .note;
			border-left-color: $color-8;

			.note-title, h2, h3, h4, h5, h6 {
				@extend .note-title;
				@extend .color-green; } }

		&-orange {
			@extend .note;
			border-left-color: $color-6;

			.note-title, h2, h3, h4, h5, h6 {
				@extend .note-title;
				@extend .color-orange; } }

		&-red {
			@extend .note;
			border-left-color: $color-4;

			.note-title, h2, h3, h4, h5, h6 {
				@extend .note-title;
				@extend .color-red; } } } }
