@mixin visibilityComponent {

    .transparent, .transparency-0 {
        @include visibility(0); }


    @for $i from 1 through 9 {
        .transparency-#{$i} {
            @include visibility($i); } }


    .opaque, .transparency-10 {
        @include visibility(10); } }
