@mixin colorComponent {

	.color {
		&-inherit {
			color: inherit; }

		&-white {
			color: $color-1; }

		&-gray {
			color: $color-2; }

		&-black {
			color: $color-3; }

		&-green {
			color: $color-8; }

		&-orange {
			color: $color-6; }

		&-red {
			color: $color-4; } } }
