@mixin listComponent {

    .list {
        &-none {
            &-style {
                list-style: none; }

            &-clear {
                @extend .list-none-style;
                margin: 0;
                padding: 0; } }

        &-unstyled {
            @extend .list-none-style;

            &-clear {
                @extend .list-none-clear; } }

        @each $type in $listStyleTypes {
            &-#{$type} {
                list-style-type: $type; } }

        &-dash {
            list-style-type: none;

            li::before {
                content: "-";
                margin-right: 10px;
                font-weight: bold; } }

        @each $listPosition in $listPositions {
            &-#{$listPosition} {
                list-style-position: $listPosition; } } } }
